import React, { useState, useEffect } from "react"
import firebase from "gatsby-plugin-firebase";
import PlayerCardList from "./Player/PlayerCardList";
import { ThemeProvider } from '@material-ui/core/styles';
import theme from "./Helpers/Theme.js";
import BuyMeACoffee from "./Base/BuyMeACoffee";
import Title from "./Base/Title";
import SeasonTitle from "./Home/SeasonTitle";
import Menu from "./Menu";
import IntroDescription from "./Home/IntroDescription";
import Container from '@material-ui/core/Container';

function App() {

  const [close504090Players, setClose504090Players] = useState({close504090Players: []});
  const [true504090Players, setTrue504090Players] = useState({true504090Players: []});

  useEffect(() => {
    // Fetches cached players on Firebase
    const db = firebase.firestore();
    var players = [];

    db.collection("close504090Players").get()
      .then((querySnapshot) => {
          querySnapshot.forEach((doc) => {
              // doc.data() is never undefined for query doc snapshots
              players.push(doc.data());
          });
          setClose504090Players(players)
      })
      .catch((error) => {
          console.log("Error getting documents: ", error);
      });

    // Fetches cached players on Firebase
    var players2 = [];

    db.collection("true504090Players").get()
      .then((querySnapshot) => {
          querySnapshot.forEach((doc) => {
              // doc.data() is never undefined for query doc snapshots
              players2.push(doc.data());
          });
          setTrue504090Players(players2)
      })
      .catch((error) => {
          console.log("Error getting documents: ", error);
      });
  }, []);

  // const sortPlayers(players) = () => {
  //   return players.sort(function(a, b){
  //     return (b.points / b.games_played) - (a.points / a.games_played);
  //   });
  // }

  return (
    <>
    <div className="container-menu">

      <ThemeProvider theme={theme}>
        {/* <Menu /> */}
        <Title />
        <Container style={{backgroundColor: "white", margin: 0, maxWidth: "100%"}}>
          <Container maxWidth="md">
            <IntroDescription />
            <SeasonTitle />
            <h3>Players On Pace</h3>
            <PlayerCardList players={true504090Players}/>
            <h3>Players Close</h3>
            <PlayerCardList players={close504090Players}/>
            <BuyMeACoffee />
          </Container>
        </Container>
      </ThemeProvider>
    </div>
    </>
  );
}

export default App;
